
.imageclass{
    height: 140px;
    width: 160px;
}

.quicksearchcontainer{
    margin-left: 100px;
    margin-top: 50px;
    font-family: Poppins;
}

.quickheading{
    font-weight: bold;
    font-size: 30px;
    color: #192f60;
}

.quicksubheading{
    font-size: 20px;
    color: #8c96ab;
    margin-top: 10px;
    margin-bottom: 20px;
}

.tilecontainer{
    width: 370px;
    height: 140px;
    background-color: #ffffff;
    box-shadow: 3px 6px 10px gray;
    margin-bottom: 30px;
    margin-right: 20px;
    float: left;
}

.tileComponent1{
    width: 50%;
    float: left;
}

.tileComponent2{
    width: 50%;
    float: left;
}

.componentHeading{
    color: #192f60;
    font-size: 20px;
    font-weight: bold;
    margin-top: 25px;
}

.componentSubHeading{
    color: gray;
    font-size: 18px;
}

@media screen and (max-width:550px){
    #dropdown{ width:350px;}
    .heading{
        color: white;
        font-size: 20px;
        text-align: center;
    }
    .quicksearchcontainer{
        margin-top: 50px;
        margin-left: 50px;
    }
}

@media screen and (max-width:700px){
    #dropdown{ width:350px}
    .heading{
        color: white;
        font-size: 25px;
        text-align: center;
    }
}