
.Rectangle-5 {
    width: 260px;
    height: 682px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: var(--white);
    display: inline-block;
  }

.filter-content{
    margin-left: 26px;
    margin-top: 17px;
}

.filter-heading {
    width: 55px;
    height: 25px;
    font-family: Poppins;
    font-size: 18px;
    font-weight: 600;
    text-align: left;
    color: var(--dark-slate-blue);
}

.content {
    width: 106px;
    height: 20px;
    font-family: Poppins;
    font-size: 14px;
    font-weight: normal;
    text-align: left;
    margin-top: 10px;
    margin-bottom: 10px;
    color: var(--dark-slate-blue);
}

.select-location-dropdown {
    width: 208px;
    height: 35px;
    border-radius: 3px;
    border: solid 1px #8c96ab;
    font-size: 20px;
}

.item {
    width: 794px;
    height: 270px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    margin-bottom: 20px;
}

.hotel_name {
    width: 100%;
    height: 43px;
    font-family: Poppins;
    font-size: 25px;
    font-weight: 600;
    text-align: left;
    color:darkblue;
    margin-top: 39px;
}

.image {
    width: 120px;
    height: 120px;
    border-radius: 19px;
    margin-left:35px;
    margin-top: 25px;
    
}

.city_name {
    width: 100%;
    height: 23px;
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    text-align: left;
    color: #192f60;
    
}

.cuisine_block {
    font-family: Poppins;
    font-size: 16px;
    font-weight: normal;
    text-align: left;
    color: #636f88;
    padding-left: 25%;
}

.cuisine_out {
    font-family: Poppins;
    font-size: 16px;
    font-weight: normal;
    text-align: left;
    color: #192f60;
}

@media screen and (max-width: 570px){
    .Rectangle-5{
        visibility: hidden;
    }

    .item {
        width: 100%;
        height: 270px;
        margin-bottom: 20px;
    }
    
    .hotel_name {
        width: 100%;
        height: 43px;
        font-size: 20px;
        font-weight: 600;
        margin-top: 25px;
    }
    
    .image {
        width: 80px;
        height: 80px;
        border-radius: 19px;
        margin-left:35px;
        margin-top: 25px;
    }
    
    .city_name {
        width: 100%;
        height: 23px;
        font-family: Poppins;
        font-size: 13px;
        font-weight: 500;
        text-align: left;
        color: #192f60;
        
    }
    
    .cuisine_block {
        font-family: Poppins;
        font-size: 16px;
        font-weight: normal;
        text-align: left;
        color: #636f88;
        padding-left: 25%;
    }
    
    .cuisine_out {
        font-family: Poppins;
        font-size: 16px;
        font-weight: normal;
        text-align: left;
        color: #192f60;
    }
}

