
.img-responsive1{
    background: url('https://i.ibb.co/txXHcmp/food-wallpaper.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border: 1px solid white;
}


.logo{
    background-color: white;
    border-radius: 50%;
    height: 100px;
    width:100px;
    text-align: center;
    font-size: 60px;
    color: red;
    margin: auto;
    margin-top: 90px;
}

.heading{
    color: white;
    font-size: 30px;
    text-align: center;
    margin: auto;
    margin-top: 19px;
    margin-bottom: 10px;
}

.locationselector{
    margin-top: 15px;
    margin-bottom: 30px;
    text-align: center;
}

#dropdown{
    width: 250px;
    height: 60px;
    border-radius: 3%;
    font-size: 20px;
    border: none;
}

.restinput{
    width: 350px;
    height: 60px;
    border-radius: 3%;
    font-size: 20px;
    border: none;
    margin-left: 10px;
    margin-top: 20px;
}